import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { withRouter } from "react-router";
import Dropdown from "Component/Dropdown";
import Search from "Component/Search";
import {
  getStockTakeSummary,
  removeEntryModule,
  saveJobSheet,
} from "apiServices";
import SKUItem from "Component/SKUItem";
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import "./index.scss";
import { selectSku } from "./services";
import {
  filterBySkuName,
  resetJobSheet,
  responseCheck,
  selectedSkuCheck,
  setSwitch,
} from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";
import PopUpUnfinished from "Component/Modal/PopUpUnfinished";
import Discard from "Component/Modal/PopUpDiscard";
import SingleView from "Component/SingleViewModal";
import { ModalsConfirmation as Confirmation } from "Component/Modal/ModalConfirmation";
import PopUpSaved from "Component/Modal/PopUpSaved";
import { deleteSentSKU } from "services";
import {
  useDimension,
  useStockTake,
  useSingleView,
  useScanner,
} from "Page/StockEntry/GeneralServices";
import { CSwitch } from "@coreui/react";
import ItemListModal from "Component/ItemListModal";
import Scanner from "Component/Scanner";

const StockTake = (props) => {
  //search
  const [category, setCategory] = useState();
  const [sortBy, setSortBy] = useState();

  const dispatch = useDispatch();
  const story = useHistory();
  const { height, emptyItem, width } = useDimension(400);
  const {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    status,
    isLoading,
    stockTakeEntryData,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    search,
    setSearch,
    area,
    areaId,
    setAreaId,
    discard,
    setDiscard,
    zeroQuantity,
    isCentral,
  } = useStockTake({ dispatch });

  const {
    updateList,
    action,
    setAction,
    confirmation,
    setConfirmation,
    isSuccess,
    setIsSuccess,
    isSaved,
    setIsSaved,
    createPo,
    setCreatePo,
    singleViewData,
    setSingleViewData,
    fromBarcode,
    setFromBarcode,
    postInfo,
  } = useSingleView({
    dispatch,
    setLocalEntryData,
    setSummary,
    setSearch,
    getStockTakeSummary,
    search,
    warehouseUuid,
    areaId,
    localEntryData,
    moduleName,
    category,
  });

  const { showScanner, setShowScanner, showSkuList, setShowSkuList } =
    useScanner();
  const isCheckedTotal = selectedSkuCheck({
    moduleEntry: localEntryData,
    moduleName,
  });
  //Condition
  const isPlural = isCheckedTotal > 1 ? "s" : "";
  const noLength = summary?.length ? "flex-column" : "flex-row";
  const isReady = localEntryData;
  // isReady =
  //   thereAreRequest === 0 &&
  //   localEntryData &&
  //   localEntryData?.jobPo

  //input type for hub
  const [inputType, setInputType] = useState(isCentral ? "uom" : "uos");

  const searchChecking = ({
    areaId,
    isCentral,
    inputType,
    setAreaId,
    setInputType,
    go,
  }) => {
    let doSearch = false;
    if (!areaId?.value) setAreaId("empty");
    if (areaId?.value && !isCentral && inputType?.value) doSearch = true;
    if (areaId?.value && !isCentral && !inputType?.value)
      doSearch = setInputType("empty");
    if (areaId?.value && isCentral) doSearch = true;

    if (doSearch) go();
  };
  return (
    <div className="w-100  stock-take-summary">
      <ItemListModal
        moduleName={moduleName}
        areaId={areaId}
        areaName={area}
        warehouseUuid={warehouseUuid}
        showSkuList={showSkuList}
        setShowSkuList={setShowSkuList}
        dispatch={dispatch}
        setLocalEntryData={setLocalEntryData}
        localEntryData={localEntryData}
        setCreatePo={setCreatePo}
        thereAreRequest={thereAreRequest}
        setShowScanner={(value) => setShowScanner(value)}
      />
      <Discard
        discard={discard}
        module={"Inventory Entry - Stock Take"}
        moduleEntry={stockTakeEntryData}
        close={() => setDiscard(false)}
        discardEntry={(id) =>
          removeEntryModule({ id, moduleName, dispatch, setDiscard })
        }
      />
      {/* <PopUpUnfinished
        discard={discard}
        moduleFirstLoad={moduleFirstLoad}
        moduleEntry={stockTakeEntryData}
        redirect={() => props.history.push("/inventory-entry/stock-take/weight-entry")}
        previousPage={() => story.goBack()}
        setDiscard={() => setDiscard(true)}
      /> */}
      <PopUp refresh={() => updateList()} status={status} />
      <SingleView
        fromBarcode={fromBarcode}
        status={status}
        moduleName={moduleName}
        moduleEntry={localEntryData}
        areaMovement={area}
        createPo={createPo}
        setCreatePo={setCreatePo}
        setSingleViewData={setSingleViewData}
        setConfirmation={setConfirmation}
        confirmation={confirmation}
      />

      <PopUpSaved
        moduleEntry={singleViewData}
        submit={() => updateList()}
        confirmation={confirmation}
        setConfirmation={setConfirmation}
        isSaved={isSaved}
        action={action}
        setIsSaved={setIsSaved}
        moduleName={moduleName}
        totalSku={localEntryData?.jobPost?.list.length}
        setLocalEntryData={setLocalEntryData}
      />

      <Confirmation
        noSkuChecking={false}
        isLoading={isLoading}
        singleViewData={singleViewData}
        moduleEntry={localEntryData}
        confirmation={confirmation}
        setConfirmation={setConfirmation}
        isSuccess={postInfo}
        setAction={setAction}
        action={action}
        submit={() =>
          deleteSentSKU({
            summary: summary,
            resetSummary: setSummary,
            singleViewData: singleViewData,
            name: "submit",
            moduleEntry: localEntryData,
            moduleName,
            dispatch,
            setIsSuccess,
            saveJobSheet,
            setLocalEntryData,
            setCreatePo,
          })
        }
      />
      {showScanner ? (
        <Scanner
          setLocalEntryData={setLocalEntryData}
          setShowSkuList={(value) => setShowSkuList(value)}
          showScanner={showScanner}
          setShowScanner={(value) => setShowScanner(value)}
          setSearch={(value) => setSearch(value)}
          search={search}
        />
      ) : (
        false
      )}

      <div className="px-3 pt-3 bg-white border-all radius-all d-md-flex dd-group">
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Select Area"
            placeholder="Select Area"
            options={area}
            onChangeDropdown={(selected) => setAreaId(selected)}
            selectedValue={areaId}
            showTitle={true}
          />
        </div>
        {/* <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={!isCentral}
            title="Input Type"
            placeholder='Select Input Type'
            options={[{ label: 'UOS', value: 'uos' }, { label: 'UOM', value: 'uom' }]}
            onChangeDropdown={(selected) => setInputType(selected)}
            selectedValue={inputType}
            showTitle={true}
          />
        </div> */}
      </div>

      <div className="pt-3 pb-0 pl-3 pr-3 bg-white mt-3 border-all radius-all mb-3">
        <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
          <div className="text LatoBold d-md-flex justify-content-between w-100 align-items-center sku-des">
            <div>
              Enter SKU or Description{" "}
              <span className="green">{`(${isCheckedTotal} Selected SKU${isPlural})`}</span>
            </div>
            <div className="d-flex align-items-center mt-2 mt-md-0 justify-content-between">
              <span className="emptyTextField LatoBold mr-3">
                Only show SKU that has 0 Initial Qty
              </span>
              <CSwitch
                className="float-right"
                shape="pill"
                color="success"
                size={width <= 500 ? "sm" : "md"}
                checked={zeroQuantity}
                onChange={() =>
                  isLoading
                    ? false
                    : setSwitch({
                        moduleName,
                        search,
                        updateList,
                        dispatch,
                        zeroQuantity,
                        setSummary,
                      })
                }
              />
            </div>
          </div>
        </div>
        <Search
          setSortBy={setSortBy}
          category={category}
          setCategory={setCategory}
          setAreaId={setAreaId}
          areaId={areaId}
          useScanner={true}
          showScanner={showScanner}
          barcodeScanner={(value) => {
            setShowScanner(value);
            setFromBarcode(true);
          }}
          value={(val) => setSearch(val)}
          placeholder={"Enter SKU"}
          filter={false}
          searchHandler={(desc) =>
            // searchChecking({
            //   areaId,
            //   inputType,
            //   isCentral,
            //   setAreaId,
            //   setInputType,
            //   go: () => getStockTakeSummary({
            //     category,
            //     moduleName,
            //     zeroQuantity,
            //     dispatch,
            //     desc,
            //     warehouseUuid,
            //     areaId,
            //     setModule: setSummary,
            //     module: localEntryData
            //   },
            //     setSearch(desc))
            // })

            areaId
              ? getStockTakeSummary(
                  {
                    category,
                    moduleName,
                    zeroQuantity,
                    dispatch,
                    desc,
                    warehouseUuid,
                    areaId,
                    setModule: setSummary,
                    module: localEntryData,
                  },
                  setSearch(desc)
                )
              : setAreaId("empty")
          }
        />
        <div
          className={`mb-3 mt-3 d-flex align-items-center scroll-sku ${noLength} w-100 text-center`}
          style={{ minHeight: height, maxHeight: height }}
        >
          {summary?.length ? (
            summary
              ?.sort(function (a, b) {
                return filterBySkuName(a, b, sortBy?.value);
              })
              ?.map((data, idx) => {
                return (
                  <SKUItem
                    key={idx}
                    img={data?.photo}
                    skuDescription={data?.skuName}
                    skuNumber={data?.skuNumber}
                    uomLabel="weight"
                    uomValue={data?.totalQty}
                    uom={data?.unitOfMeassures}
                    isCheckedHandler={(checked) =>
                      selectSku({
                        idx,
                        checked,
                        dispatch,
                        setLocalEntryData,
                        moduleSummary: summary,
                        moduleEntry: localEntryData,
                        moduleName,
                        warehouseUuid,
                        areaId,
                        inputType,
                        zeroQuantity,
                      })
                    }
                    check={data?.checked}
                    singleView={true}
                    createPo={createPo}
                    setCreatePo={setCreatePo}
                    setFromBarcode={setFromBarcode}
                  />
                );
              })
          ) : (
            <div
              className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center"
              style={{ minHeight: emptyItem, maxHeight: emptyItem }}
            >
              {!responseCheck({ isLoading, summary }) ? (
                <img className="loading" src={loading} alt="loading" />
              ) : areaId && summary?.length === 0 ? (
                <div>
                  Sorry, we couldn't find any matches for '
                  <span className="LatoBold">{search}</span>' in {areaId?.label}{" "}
                  area.
                  <br />
                  Please Try search with another term.
                </div>
              ) : (
                "Enter SKU"
              )}
            </div>
          )}
        </div>
        {/* <SKUPagination/> */}
      </div>
      <div className="h-screen-summary-footer d-flex flex-column justify-content-start">
        <div className="w-100 d-flex align-items-center justify-content-between mb-1">
          <button
            tabIndex="0"
            onClick={() =>
              props.history.push("/inventory-entry/stock-take/CSV-upload")
            }
            type="button"
            className="btn px-4 no-shadow sku-item border-all bg-white fontSizeButton"
          >
            Upload File
          </button>
          <button
            tabIndex="0"
            onClick={() => {
              if (isReady) {
                resetJobSheet({ localEntryData, dispatch });
                props.history.push("/inventory-entry/stock-take/weight-entry");
              }
            }}
            type="button"
            className={`btn px-5 fontSizeButton sku-item no-shadow text-white ${
              isReady ? "bg-green" : "bg-lineGrey"
            }`}
          >
            {/* {(!localEntryData || localEntryData?.jobPost?.list?.length === 0) && thereAreRequest === 0 ? 'Next' : isReady ? 'Next' : 'Processing...'} */}
            {isReady ? "Next" : "Processing..."}
          </button>
        </div>
      </div>
    </div>
  );
};

export default withRouter(StockTake);
